.reactify--disabled {
  cursor: not-allowed;
  pointer-events: none;
  opacity: 0.6; }

.reactify--hidden {
  display: none !important; }

.reactify--pl-1 {
  padding-left: 1em; }

.reactify--pr-1 {
  padding-right: 1em; }

.reactify-badge__container {
  display: inline-block; }

.reactify-badge__children {
  position: relative; }

.reactify-badge-bubble {
  position: absolute;
  color: #fff;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-content: center;
  align-items: center;
  justify-content: center;
  height: 2em;
  min-width: 2em;
  z-index: 100;
  box-shadow: 0 1px 1px 2px rgba(0, 0, 0, 0.2);
  padding: 2px; }
  .reactify-badge-bubble--theme-default {
    background-color: #141414; }
  .reactify-badge-bubble--theme-dark {
    background-color: #141414; }
  .reactify-badge-bubble--theme-light {
    background-color: #bfbfbf; }
  .reactify-badge-bubble--theme-info {
    background-color: #3498db; }
  .reactify-badge-bubble--theme-warning {
    background-color: #f39c12; }
  .reactify-badge-bubble--theme-danger {
    background-color: #e74c3c; }
  .reactify-badge-bubble--theme-success {
    background-color: #2ecc71; }
  .reactify-badge-bubble--font-size-small {
    font-size: 0.6em !important;
    border-radius: 2em; }
  .reactify-badge-bubble--font-size-medium {
    font-size: 1.2em !important;
    border-radius: 1.2em; }
  .reactify-badge-bubble--font-size-large {
    font-size: 1.8em !important;
    border-radius: 1.8em; }
  .reactify-badge-bubble--position-top-left {
    top: 0px;
    left: 0px;
    transform: translate(-50%, -50%); }
  .reactify-badge-bubble--position-top-right {
    top: 0px;
    right: 0px;
    transform: translate(50%, -50%); }
  .reactify-badge-bubble--position-bottom-left {
    bottom: 0px;
    left: 0px;
    transform: translate(-50%, 50%); }
  .reactify-badge-bubble--position-bottom-right {
    bottom: 0px;
    right: 0px;
    transform: translate(50%, 50%); }
