.reactify--disabled {
  cursor: not-allowed;
  pointer-events: none;
  opacity: 0.6; }

.reactify--hidden {
  display: none !important; }

.reactify--pl-1 {
  padding-left: 1em; }

.reactify--pr-1 {
  padding-right: 1em; }

.reactify-checkbox__container {
  display: inline-flex;
  align-content: center;
  align-items: center; }

.reactify-checkbox--font-small {
  font-size: 0.6em; }

.reactify-checkbox--font-medium {
  font-size: 1.2em; }

.reactify-checkbox--font-large {
  font-size: 1.8em; }

.reactify-checkbox__label--position-left {
  margin: 0;
  padding-right: 0.2em; }

.reactify-checkbox__label--position-right {
  margin: 0;
  padding-left: 0.2em; }

.reactify-checkbox__icon {
  cursor: pointer;
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  -khtml-user-select: none;
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none; }
  .reactify-checkbox__icon--theme-default {
    color: #141414; }
    .reactify-checkbox__icon--theme-default:hover {
      color: black; }
  .reactify-checkbox__icon--theme-dark {
    color: #141414; }
    .reactify-checkbox__icon--theme-dark:hover {
      color: black; }
  .reactify-checkbox__icon--theme-light {
    color: #bfbfbf; }
    .reactify-checkbox__icon--theme-light:hover {
      color: gray; }
  .reactify-checkbox__icon--theme-info {
    color: #3498db; }
    .reactify-checkbox__icon--theme-info:hover {
      color: #16527a; }
  .reactify-checkbox__icon--theme-warning {
    color: #f39c12; }
    .reactify-checkbox__icon--theme-warning:hover {
      color: #7f5006; }
  .reactify-checkbox__icon--theme-danger {
    color: #e74c3c; }
    .reactify-checkbox__icon--theme-danger:hover {
      color: #921e12; }
  .reactify-checkbox__icon--theme-success {
    color: #2ecc71; }
    .reactify-checkbox__icon--theme-success:hover {
      color: #176437; }
