.reactify--disabled {
  cursor: not-allowed;
  pointer-events: none;
  opacity: 0.6; }

.reactify--hidden {
  display: none !important; }

.reactify--pl-1 {
  padding-left: 1em; }

.reactify--pr-1 {
  padding-right: 1em; }

.reactify-button--margin-left {
  margin-left: 0.2em; }

.reactify-button--margin-right {
  margin-right: 0.2em; }

.reactify-button__container {
  color: white;
  cursor: pointer;
  display: inline-flex;
  align-content: center;
  align-items: center;
  padding: 0.2em 0.4em;
  border-radius: 2px;
  justify-content: space-between; }

.reactify-button__loader__container {
  font-size: 1.0em;
  display: inline-flex;
  align-items: center;
  align-content: center; }

.reactify-button__loader__loader {
  -webkit-animation: rotating 1s linear infinite;
  -moz-animation: rotating 1s linear infinite;
  -ms-animation: rotating 1s linear infinite;
  -o-animation: rotating 1s linear infinite;
  animation: rotating 1s linear infinite; }

.reactify-button--font-size-small {
