.reactify--disabled {
  cursor: not-allowed;
  pointer-events: none;
  opacity: 0.6; }

.reactify--hidden {
  display: none !important; }

.reactify--pl-1 {
  padding-left: 1em; }

.reactify--pr-1 {
  padding-right: 1em; }

.reactify-radio-input__container {
  display: inline-flex;
  align-items: center;
  min-height: 1.7em; }

.reactify-radio-input--font-small {
  font-size: 8px; }

.reactify-radio-input--font-medium {
  font-size: 16px; }

.reactify-radio-input--font-large {
  font-size: 24px; }

.reactify-radio-input__label--position-left {
  margin: 0;
  padding-right: 0.5em; }

.reactify-radio-input__label--position-right {
  margin: 0;
  padding-left: 0.5em; }

.reactify-radio-input__input {
  height: 1.6em;
  width: 1.6em;
  position: relative;
  cursor: pointer;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  padding: 0.1em; }
  .reactify-radio-input__input::after {
    content: '';
    width: 100%;
    height: 100%;
    background-color: transparent;
    position: absolute;
    border-radius: 100%;
    padding: .2em;
    border: .1em solid darkgray;
    pointer-events: none;
    background-clip: content-box;
    transition: .2s; }
  .reactify-radio-input__input:checked::after {
    background-color: currentColor;
    border: 0.1em solid currentColor; }
  .reactify-radio-input__input:hover:not(:checked)::after {
    background-color: #cfcfcf;
    border: 0.1em solid #cfcfcf; }
  .reactify-radio-input__input:focus {
    outline: none; }
  .reactify-radio-input__input--theme-default:checked::after {
    background-color: #141414;
    border: 0.1em solid #141414; }
  .reactify-radio-input__input--theme-dark:checked::after {
    background-color: #141414;
    border: 0.1em solid #141414; }
  .reactify-radio-input__input--theme-light:checked::after {
    background-color: #bfbfbf;
    border: 0.1em solid #bfbfbf; }
  .reactify-radio-input__input--theme-info:checked::after {
    background-color: #3498db;
    border: 0.1em solid #3498db; }
  .reactify-radio-input__input--theme-warning:checked::after {
    background-color: #f39c12;
    border: 0.1em solid #f39c12; }
  .reactify-radio-input__input--theme-danger:checked::after {
    background-color: #e74c3c;
    border: 0.1em solid #e74c3c; }
  .reactify-radio-input__input--theme-success:checked::after {
    background-color: #2ecc71;
    border: 0.1em solid #2ecc71; }
