.reactify--disabled {
  cursor: not-allowed;
  pointer-events: none;
  opacity: 0.6; }

.reactify--hidden {
  display: none !important; }

.reactify--pl-1 {
  padding-left: 1em; }

.reactify--pr-1 {
  padding-right: 1em; }

.reactify-label__container {
  display: inline-block;
  border-radius: 0.2em;
  padding: 0px .2em; }

.reactify-label--font-size-small {
  font-size: 0.6em; }

.reactify-label--font-size-medium {
  font-size: 1.2em; }

.reactify-label--font-size-large {
  font-size: 1.8em; }

.reactify-label--theme-default {
  background-color: #141414;
  color: white; }

.reactify-label--theme-dark {
  background-color: #141414;
  color: white; }

.reactify-label--theme-light {
  background-color: #bfbfbf;
  color: black; }

.reactify-label--theme-info {
  background-color: #3498db;
  color: white; }

.reactify-label--theme-warning {
  background-color: #f39c12;
  color: white; }

.reactify-label--theme-danger {
  background-color: #e74c3c;
  color: white; }

.reactify-label--theme-success {
  background-color: #2ecc71;
  color: white; }
